import React from 'react';

import styles from 'styles/post.module.scss';
import burn from './burn.gif';
import gokul from './gokul.png';
import PlaystoreBanner from 'components/play-store';

class BasicsEmergency extends React.Component {

  render(){
    return (
      <div className={styles["post"]}>
          <div className={styles["header"]}>
            <h3 className={styles["sub-title"]}>L1 : THE ABSOLUTE BASICS</h3>
            <div className={styles["title-box"]}>
              <div className={styles["container"]}>
                <h3 className={styles["title"]}>Emergency Fund</h3>
                <div className={styles["title-line"]}></div>
              </div>
            </div>
          </div>
          <div className={styles["post-content"]}>
            <div className={styles["tldr"]}>
              <h2 className={styles["heading"]}>‚ö°Ô∏è Here‚Äôs what this lesson is all about :</h2>
              <p>Shit Happens.</p>
              <p>A person in China ate a bat and the stock market crashed and millions of people lost their jobs.</p>
              <p>You need to save 4-6 months of living expenses in a safe place, where it won't get affected by inflation.</p>
              <br/>
              <p>Read on to understand how you should go about it üëá</p>
            </div>
            <p>The best way to explain the need for an emergency fund would be with an anecdote.</p><br/>
            <div className={styles["box"]}>
              <h5>&#10024; STORY TIME &#10024;</h5>
              <img src={gokul} style={{width: "50px"}}/>
              <br/>
              <p>Hi üëã, I‚Äôm Gopi ,one of the cofounders of Savemo. I was a person who saved money every month. I used to invest all the money I saved either into stocks or into Long term mutual Funds.</p>
              <br/>
              <p>In March, I quit my job to start Savemo and I thought I'll redeem my shares and mutual funds a bit every month to take care of my expenses.</p>
              <br/>
              <p>I think you can figure out where this is going. Like an ideal party-pooper, COVID-19 ruined everything üò∑.</p>
              <br/>
              <img src={burn} alt="burning"/>
              <br/>
              <p>A week after I quit my job, the Stock Market crashed and all my holdings lost a lot of value. (How much you ask? Just a mere 40% drop in value üò≠)</p>
              <p>So in a day, I went from 12 months worth of Savings to around 7 months. #Relatable ?</p>
              <br/>
            </div>
            <br/>
            <p>So where did I fuck up?</p>
            <p>üëá</p>
            <br/>
            <p>I invested the money which I needed for the short term/emergency in things that were supposed to give me a return over the long term.</p>
            <br/>
            <p>What should I have done instead?</p>
            <p>üëá</p>
            <p>Put at least 4-6 months worth of living expenses in a safe place whose value won't go down and then invest the rest in places that give you returns over the long term.</p>
            <br/>
            <p>Remember that inflation applies here too. If you save this money in a Savings account, the value of your money keeps reducing every year. </p>
            <br/>
            <p className={styles["highlight"]}>So you need to keep this money in a place that is safe and gives you enough returns to atleast beat inflation ( &lt;5%)</p>
            <br/>
            <p>We'll help you figure all of this out in the next chapters. For now, just keep in mind that an emergency fund should ideally cover atleast 6 months of your expenses.</p>
            <br/>
            <PlaystoreBanner text={"Wanna try Savemo Pocket?"}/>
          </div>
      </div>
    );
  }
}

export default BasicsEmergency;