import {Switch, Route} from 'react-router-dom';
import React from 'react';

import BasicsCompounding from 'pages/basics/compounding';
import BasicsInflation from 'pages/basics/inflation';
import BasicsEmergency from 'pages/basics/emergency-fund';
import BasicsBudgeting from 'pages/basics/budgeting';

class Basics extends React.Component {

  render(){
    return (
        <Switch>
            <Route path="/post/the-absolute-basics/compounding"><BasicsCompounding/></Route>
            <Route path="/post/the-absolute-basics/inflation"><BasicsInflation/></Route>
            <Route path="/post/the-absolute-basics/emergency-fund"><BasicsEmergency/></Route>
            <Route path="/post/the-absolute-basics/budgeting"><BasicsBudgeting/></Route>
            <Route path="/post/the-absolute-basics"><BasicsCompounding/></Route>
        </Switch>
    );
  }
}

export default Basics;