import React from 'react';

import styles from './styles.module.scss';
import arrow from './arrow.svg';

class Calculator extends React.Component{

    constructor(props){
        super(props);
        this.state = {
            duration: 2,
            start: 0,
            startSelected: false,
            return: 5,
            returnSelected: false,
            invest: true,
            worth: 0,
            inlfationWorth: 0,
        }
        this.starts = [5000,10000,15000,20000,30000];
        this.duration = [10,20,30,40,50];
        this.returns = [5,6,7,8,9,10,11,12];
    }
    componentDidMount(){
        this.calculate();
    }

    changeDuration(x){
        return ()=>{
            this.setState({duration: x}, this.calculate);
        }
    }

    changeStart(x){
        return (e)=>{
            e.preventDefault();
            this.setState({start: x, startSelected: false}, this.calculate);
        }
    }

    changeReturn(x){
        return (e)=>{
            e.preventDefault();
            this.setState({return: x, returnSelected: false}, this.calculate);
        }
    }

    calculate(){
        var rate = this.returns[this.state.return];
        var inflationRate = -5
        rate = 1+(rate*0.01);
        inflationRate = 1+(inflationRate*0.01);
        var multiplier=0;
        var inflationMultiplier=0;
        var duration = this.duration[this.state.duration];
        for(var i=duration;i>0;i--){
            multiplier += Math.pow(rate,i);
            inflationMultiplier += Math.pow(inflationRate,i);
        }
        this.setState({inflationWorth: (this.starts[this.state.start]*inflationMultiplier*12/100000).toFixed(2), worth: (this.starts[this.state.start]*multiplier*12/100000).toFixed(2)});
    }

    render(){
        return (
            <div className={styles["calculator"]}>
                <h5>Stay invested for</h5>
                <div className={styles["duration-container"]}>
                    <div className={styles["duration"]} onClick={this.changeDuration(0)}>10 Yrs</div>
                    <div className={styles["duration"]} onClick={this.changeDuration(1)}>20 Yrs</div>
                    <div className={styles["duration"]} onClick={this.changeDuration(2)}>30 Yrs</div>
                    <div className={styles["duration"]} onClick={this.changeDuration(3)}>40 Yrs</div>
                    <div className={styles["duration"]} onClick={this.changeDuration(4)}>50 Yrs</div>
                    <div className={styles["highlight"]} style={{left: ((20*this.state.duration))+"%"}}></div>
                </div>

                <div className={styles["variables"]}>
                    <div className={styles["variable"]}>
                        <label>Save monthly</label>
                        <div className={styles["select"]}>
                            <div className={styles["value"]} onClick={()=>{this.setState({startSelected: !this.state.startSelected})}}>
                                <span>{this.starts[this.state["start"]]}</span>
                                <span><img src={arrow}/></span>
                            </div>
                            <div className={styles["options"] + (this.state.startSelected?" "+styles["selected"]:"")}>
                                <div className={styles["option"]} onClick={this.changeStart(0)}>5000</div>
                                <div className={styles["option"]} onClick={this.changeStart(1)}>10000</div>
                                <div className={styles["option"]} onClick={this.changeStart(2)}>15000</div>
                                <div className={styles["option"]} onClick={this.changeStart(3)}>20000</div>
                                <div className={styles["option"]} onClick={this.changeStart(4)}>30000</div>
                            </div>
                        </div>
                    </div>
                    <div className={styles["variable"]}>
                        <label>Returns</label>
                        <div className={styles["select"]}>
                            <div className={styles["value"]} onClick={()=>{this.setState({returnSelected: !this.state.returnSelected})}}>
                                <span>{this.returns[this.state["return"]]} %</span>
                                <span><img src={arrow}/></span>
                            </div>
                            <div className={styles["options"] + (this.state.returnSelected?" "+styles["selected"]:"")}>
                                <div className={styles["option"]} onClick={this.changeReturn(0)}>5 %</div>
                                <div className={styles["option"]} onClick={this.changeReturn(1)}>6 %</div>
                                <div className={styles["option"]} onClick={this.changeReturn(2)}>7 %</div>
                                <div className={styles["option"]} onClick={this.changeReturn(3)}>8 %</div>
                                <div className={styles["option"]} onClick={this.changeReturn(4)}>9 %</div>
                                <div className={styles["option"]} onClick={this.changeReturn(5)}>10 %</div>
                                <div className={styles["option"]} onClick={this.changeReturn(6)}>11 %</div>
                                <div className={styles["option"]} onClick={this.changeReturn(7)}>12 %</div>
                            </div>
                        </div>
                    </div>
                </div>
                <p>You'd be worth</p>
                <div className={styles["result-container"]}>
                    <div className={styles["result"]}>
                        <div className={styles["emoji"]}>üíÄ</div>
                        <span className={styles["value"]}>‚Çπ{this.state.inflationWorth>100?(this.state.inflationWorth/100).toFixed(2):this.state.inflationWorth} {this.state.inflationWorth>100?"Crores":"Lakhs"}</span>
                        <p>if you kept it in<br/>your locker</p>
                    </div>
                    <div className={styles["result"]}>
                        <div className={styles["emoji"]}>üòç</div>
                        <span className={styles["value"]}>‚Çπ{this.state.worth>100?(this.state.worth/100).toFixed(2):this.state.worth} {this.state.worth>100?"Crores":"Lakhs"}</span>
                        <p>if you had stayed<br/>invested</p>
                    </div>
                </div>
            </div>
        )
    }
}

export default Calculator;